@import url(https://fonts.googleapis.com/css?family=Work+Sans:300,400,500,700&display=swap);
body {
  /* background: linear-gradient(180deg, rgba(248,177,149,1) 0%, rgba(192,108,132,1) 35%, rgba(53,92,125,1) 100%) */
  padding: 2rem;
  background: rgb(27, 27, 27);
  font-family: "Work Sans", sans-serif;
  color: #fff;
  background-image: -webkit-gradient(linear, left top, right top, from(#868f96), to(#596164));
  background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
  background-image: -webkit-gradient(linear, left bottom, left top, from(#c79081), to(#dfa579));
  background-image: linear-gradient(to top, #c79081 0%, #dfa579 100%);
  background-image: -webkit-gradient(linear, left top, right top, from(#a8caba), to(#5d4157));
  background-image: linear-gradient(to right, #a8caba 0%, #5d4157 100%);
  background-image: linear-gradient(-60deg, #ff5858 0%, #f09819 100%);
  background-image: linear-gradient(45deg, #874da2 0%, #c43a30 100%);
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(#ff8177),
    color-stop(0%, #ff867a),
    color-stop(21%, #ff8c7f),
    color-stop(52%, #f99185),
    color-stop(78%, #cf556c),
    to(#b12a5b)
  );
  background-image: linear-gradient(
    to right,
    #ff8177 0%,
    #ff867a 0%,
    #ff8c7f 21%,
    #f99185 52%,
    #cf556c 78%,
    #b12a5b 100%
  );
  background-image: linear-gradient(
    -225deg,
    #231557 0%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
  );
  background-image: -webkit-gradient(linear, left bottom, left top, from(#50cc7f), to(#f5d100));
  background-image: linear-gradient(to top, #50cc7f 0%, #f5d100 100%);
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
}

.imageSearch {
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
  position: relative;
}

.imageSearch__form input {
  margin: 0 auto;
  background-color: transparent;
  border: transparent;
  border-bottom: solid 2px #34495e;
  padding-bottom: 8px;
  width: 25%;
  font-weight: 200;
  letter-spacing: 2px;
  font-size: 16px;
  color: #fff;
  margin-right: 10px;
}

.imageSearch__form input:focus {
  outline: none;
}

.imageSearch__form button {
  border: 0;
  width: 7%;
  position: absolute;
  top: 4px;
  background: transparent;
  border: solid 2px #34495e;
  color: #fff;
}

.imageList__container {
  border-radius: 1px;
  box-shadow: -1px 2px 34px -13px rgba(0, 0, 0, 0.55);
  text-align: left;
  overflow: hidden;
}

.imageList__image {
  width: 100%;
  height: 200px;
}

.image__details {
  background: #34495e;
}

.image__details button {
  width: 100%;
  border: 0;
  background-color: #2c3e50;
  color: #fff;
}

.imageView__container {
  width: 100%;
  text-align: center;
  color: #fff;
}

.imageView__img {
  height: 400px;
  width: auto;
}

.imageView__copyright {
  color: #8f8f8f;
}

.imageView__text {
  margin-top: 1rem;
}

.imageView__text h4,
h5,
span {
  letter-spacing: 1px;
  color: #f39c12;
}

.imageView__text h4 span {
  font-weight: bold;
  color: #c0392b;
}

.imageView__text a {
  font-weight: bold;
  color: #c0392b;
}

.imageView__text button {
  border: 0;
  background: transparent;
  border: solid 2px #34495e;
  margin-top: 10px;
}

